// app/solar-battery/specs/page.tsx
import fs from 'fs'
import path from 'path'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import 'highlight.js/styles/github-dark.css' // 暗模式更好看

import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel"

// 你的图片放 public/images/solar/ 目录下，或者用在线链接也行
const images = [
  "/images/solar/1.jpg",
  "/images/solar/2.jpg",
  "/images/solar/3.jpg",
  "https://images.unsplash.com/photo-1509395593452-1b8dd447a73c?w=1200&h=800&fit=crop",
  "https://images.unsplash.com/photo-1501436513145-521a68d1f4f5?w=1200&h=800&fit=crop",
]

const mdFilePath = path.join(process.cwd(), 'app/_post/page.md')

export default async function SolarBatterySpecs() {
  const mdContent = await fs.promises.readFile(mdFilePath, 'utf8')

  return (
    <div className="max-w-5xl mx-auto p-6 space-y-12">
      {/* 轮播图放在最上面 */}
      <Carousel className="w-full max-w-4xl mx-auto">
        <CarouselContent>
          {images.map((src, index) => (
            <CarouselItem key={index}>
              <div className="relative aspect-video overflow-hidden rounded-xl bg-muted">
                <img
                  src={src}
                  alt={`太阳能电池图 ${index + 1}`}
                  className="w-full h-full object-cover"
                  // 可选：加载失败兜底
                  onError={(e) => {
                    e.currentTarget.src = "https://via.placeholder.com/1200x800?text=Image+Not+Found"
                  }}
                />
                <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/70 to-transparent p-6">
                  <p className="text-white text-lg font-medium">
                    太阳能电池实拍图 {index + 1}
                  </p>
                </div>
              </div>
            </CarouselItem>
          ))}
        </CarouselContent>
        <CarouselPrevious />
        <CarouselNext />
      </Carousel>

      {/* Markdown 正文 */}
      <article className="prose prose-lg dark:prose-invert max-w-none">
        <ReactMarkdown remarkPlugins={[remarkGfm]}>
          {mdContent}
        </ReactMarkdown>
      </article>
    </div>
  )
}